header {
    padding: 15px 0;
    background-color: $primary-blue; }

.header-container {
    height: 30px;
    display: flex;
    align-items: center; }

.header {
    &__logo-link {
        display: block; }

    &__logo-img {
        display: block;
        width: 147px;

        &_news {
            margin-bottom: 12px; }

        &_event {
            margin-bottom: 11px; }

        &_history {
            margin-bottom: 9px; } }

    &__nav {
        margin-left: auto;
        margin-right: 25px; }

    &__search-btn {
        display: block;
        width: 43px;
        height: 18px;
        padding-left: 25px;
        color: $light-c;
        font-size: $base-fz; } }

.weather-widget img {
    display: block;
    height: 30px; }
