.film {
    margin-left: 110px; }

.film-header {
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px; }

.film-cover {
    display: block;
    width: 240px;
    margin-right: 40px;
    flex-shrink: 0; }

.film-info {
    flex-grow: 1; }

.film-title {
    margin-bottom: 20px;
    font-family: $bold-f;
    font-size: 22px;
    letter-spacing: 0.02em;
    line-height: $micro-lh; }

.film-info-table {
    td {
        padding: 5px 0;

        img {
            display: block;
            width: 32px;
            height: 32px; } }

    tr:nth-child(1) td {
        padding-top: 0; }

    tr:last-child td {
        padding-bottom: 0; }

    td:nth-child(1) {
        width: 160px;
        font-size: 12px;
        line-height: $base-lh; }

    td:nth-child(2) {
        font-size: 16px;
        line-height: $mini-lh; } }

.film-timetable {
    margin-bottom: 40px;
    padding-bottom: 20px;
    border-bottom: 1px solid #b1c1d2;

    &__days {
        margin-bottom: 40px; }

    &__days,
    &__times {
        display: flex;
        align-items: center;
        justify-content: space-between; }

    &__day-btn {
        flex-shrink: 0;
        padding: 20px 10px;
        border-radius: 4px;
        line-height: $micro-lh;

        &:nth-child(1) {
            width: 113px; }

        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6) {
            width: 98px; }

        &:nth-child(7) {
            width: 110px; }

        &:not(:last-child) {
            margin-right: 6px; }

        &:hover {
            color: #FF231C; }

        &.active, {
            background: linear-gradient(#ff4006 0%, #ff0038 100%);
            color: $light-c; } } }

.film-description {
    margin-bottom: 20px; }

.film-treiler {
    height: 420px;
    position: relative;

    iframe {
        position: absolute;
        left: 0;
        height: 100%;
        width: 100%; } }

.day-btn__week-day {
    margin-bottom: 10px;
    font-size: 15px; }

.day-btn__day {
    margin-bottom: 10px;
    font-size: 36px;
    font-family: $bold-f; }

.day-btn__month {
    font-size: 15px; }

.film-seance-wrap {
    display: flex; }

.film-seance {
    padding: 15px;
    text-align: left;

    &:nth-child(1) {
        width: 113px; }

    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6) {
        width: 98px; }

    &:nth-child(7) {
        width: 110px; }

    &_3d {
        position: relative;

        &::after {
            content: url(../images/svg/3d-icon.svg);
            display: block;
            width: 26px;
            height: 20px;
            position: absolute;
            bottom: 15px;
            right: 8px; } }

    &:not(:last-child) {
        margin-right: 6px; }

    &__time {
        height: 28px;
        font-size: 22px;
        font-family: $bold-f; }

    &__price {
        display: inline-block;
        height: 20px;
        font-size: 16px;
        color: #7A8BA1; }

    &__buy-btn {
        margin-left: auto; } }
