.cinema-widget {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    background-color: #283348;
    @extend %border;

    &__title {
        margin-bottom: 20px;
        font-family: $bold-f;
        font-size: 24px;
        letter-spacing: 0.02em;
        line-height: 24px;
        color: #fff;
        text-align: center; }

    &__slider {
        height: 390px;
        margin-left: 20px;
        margin-right: 20px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end; }

    .slider-circles-container {
        position: relative;
        bottom: -15px;
        left: 0;
        transform: none;
        margin-left: auto;
        margin-right: auto; }

    .slider-arrow-container {
        position: relative;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: $light-c; } }
