.index-manage-grid {
    display: grid;
    grid-template-columns: 3fr 3fr 2fr 4fr;
    grid-template-rows: 480px 1fr;
    grid-gap: 20px;

    & > :nth-child(1) {
        grid-column: 4/5;
        grid-row: 1/2; }

    & > :nth-child(2) {
        grid-column: 1/4;
        grid-row: 1/2; }

    & > :nth-child(3) {
        grid-column: 1/2;
        grid-row: 2/3; }

    & > :nth-child(4) {
        grid-column: 2/3;
        grid-row: 2/3; }

    & > :nth-child(5) {
        grid-column: 3/5;
        grid-row: 2/3; } }

.base-3items-grid {
    display: grid;
    grid-template-columns: 3fr 3fr 6fr;
    grid-gap: 20px;
    margin-top: 20px; }

.base-4items-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    margin-top: 20px; }

.todo-layout {
    display: grid;
    grid-template-columns: repeat(4, 3fr);
    grid-gap: 20px;

    & > :nth-child(1) {
        grid-column: 4/5;
        grid-row: 1/2; }

    & > :nth-child(2) {
        grid-column: 1/3;
        grid-row: 1/2; }

    & > :nth-child(3) {
        grid-column: 3/4;
        grid-row: 1/2; }

    & > :nth-child(4) {
        grid-column: 1/2;
        grid-row: 2/3; }

    & > :nth-child(5) {
        grid-column: 2/3;
        grid-row: 2/3; }

    & > :nth-child(6) {
        grid-column: 3/4;
        grid-row: 2/3; }

    & > :nth-child(7) {
        grid-column: 4/5;
        grid-row: 2/3; } }
