.offer-widget {
    position: relative;
    padding: 30px 60px 30px 30px;
    border-radius: 4px;
    border: 2px solid;

    &_news {
        border-color: #9c00ce;

        .offer-widget__btn {
            background-color: #9c00ce; }

        .offer-widget__icon {
            color: rgba(#9c00ce, .1);

            &::before {
                font-size: 90px; } } }

    &_event {
        border-color: #FF4006;

        .offer-widget__btn {
            background-color: #FF4006; }

        .offer-widget__icon {
            color: rgba(#FF4006, .1);

            &::before {
                font-size: 80px; } } }


    &_history {
        border-color: #0073FF;

        .offer-widget__btn {
            background: linear-gradient(to right, #4840ff 0%, #0094ff 100%); }

        .offer-widget__icon {
            color: rgba(#0073FF, .1);

            &::before {
                font-size: 80px; } } }

    &__text {
        margin-bottom: 20px;
        font-size: $mini-fz;
        color: $dark-c; }

    &__btn {
        color: $light-c;
        letter-spacing: 0.04em; }

    &__icon {
        position: absolute;
        bottom: 15px;
        right: 20px;
        font-size: 0; } }
