.publication {
    margin-bottom: 20px;

    &__cover-wrap {
        position: relative;

        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
            background: linear-gradient(rgba(0, 51, 104, 0) 0%, rgba(8, 44, 86, 0.02) 77.39%, rgba(9, 44, 85, 0.25) 91.63%, rgba(23, 33, 54, 0.8) 100%);
            opacity: .6; } }

    &__cover {
        display: block;
        position: relative;
        z-index: 1; }

    &__img-figcaption {
        position: absolute;
        bottom: 15px;
        left: 30px;
        right: 30px;
        z-index: 3;
        font-size: 0; }

    &__img-description {
        margin-right: 20px; }

    &__img-description,
    &__img-author {
        display: inline-block;
        width: 240px;
        font-size: 14px;
        letter-spacing: 0.08em;
        line-height: 20px;
        color: #dde2e5; }

    &__content {
        margin-top: 20px; }

    &__date {
        margin-bottom: 20px;
        font-size: 14px;
        letter-spacing: 0.08em;
        line-height: 20px;
        color: #7a7a7a; }

    &__event-info {
        margin-right: 50px; }

    &__event-info-wrap {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        font-size: 16px;
        font-family: $bold-f;

        [class^="icon-"] {
            display: inline-block;
            margin-right: 5px;
            font-size: 18px;
            color: #0055FF;
            vertical-align: bottom; }

        a {
            color: #0055FF;
            text-decoration: underline;

            &:hover {
                text-decoration: none; } } }

    &__title {
        margin-bottom: 20px;
        font-size: $huge-fz;
        font-family: $bold-f; }

    &__lead {
        margin-bottom: 20px;
        font-size: 21px;
        font-family: $additional-f;
        line-height: $mini-lh; }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 60px; }

    &__author {
        font-size: 18px; } }
